import celulares from '../json/celulares';

function ascendingOrder(){
  celulares.sort((a, b) => {
    return a.modelo.toLowerCase() > b.modelo.toLowerCase() ? 1 :  -1;
  });
}

function descendingOrder(){
  celulares.sort((a, b) => {
    return a.modelo.toLowerCase() < b.modelo.toLowerCase() ? 1 :  -1;
  });
}

function ascendingPrizes(){
  celulares.sort((a, b) => {
    return Number(a.precio) < Number(b.precio) ? 1 :  -1;
  });
}

function descendingPrizes(){
  celulares.sort((a, b) => {
    return Number(a.precio) > Number(b.precio) ? 1 :  -1;
  });
}

const order = () =>{
  const options = document.querySelectorAll('option'),
  checkboxes = document.querySelectorAll("input[type='checkbox']");

console.log(checkboxes)

  for (let x of checkboxes){
    x.checked = true;
  }

     if (options[0].selected){
      ascendingOrder();
     } else if (options[1].selected){
      descendingOrder();
     } else if (options[2].selected){
      ascendingPrizes();
     } else {
      descendingPrizes();
     }
  return celulares;
}

export default order;
